









































































.split-pane {
  position: relative;
  width: 100%;
  height: 100%;

  .pane {
    position: absolute;
    top: 0;
    bottom: 0;
  }

  .left-pane {
    left: 0;
  }

  .right-pane {
    right: 0;
    padding: 10px 10px 10px 16px;
    overflow: auto;
  }

  .split-line {
    width: 6px;
    height: 100%;
    background: #eee;
    z-index: 10;
    cursor: col-resize;
  }
}
