.online-run {
  position: absolute;
  width: 100%;
  background: white;
  z-index: 1000;

  .vue-codemirror,
  .CodeMirror {
    height: 100%;
  }

  .codemirror-head {
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
            align-items: center;
    height: 70px;
    padding: 0 10px;
  }

  .main {
    border: 1px solid $border;
    height: calc(100vh - 80px);
    min-height: 800px;
  }

